@import url(https://fonts.googleapis.com/css2?family=Lora:ital,wght@1,500&family=Roboto:wght@300&display=swap);
*{margin:0;padding:0;box-sizing:border-box}.App{min-height:100vh;display:flex;flex-direction:row;justify-content:center;align-items:center;background-color:#7F8688}.App #quote-box{margin:0 auto;width:90%}.App #quote-box .display_quote{width:100%;padding:1em;min-height:300px;background-color:#929D9E;display:flex;flex-direction:column;justify-content:center;text-align:center}.App #quote-box .display_quote #text{font-family:'Lora';font-size:7vw;color:#F3E367}.App #quote-box .display_quote #author{margin-top:1em;font-family:"Roboto", Verdana, sans-serif;font-size:4vw;color:#F3E367}.App #quote-box .display_buttons{width:100%;height:100px;padding:0 1em;display:flex;justify-content:space-between;align-items:center;background-color:#929D9E}.App #quote-box .display_buttons a,.App #quote-box .display_buttons button{height:35%}.App #quote-box .display_buttons a{text-decoration:none;display:flex;align-items:center}.App #quote-box .display_buttons a .fab{font-size:2em;color:#F3E367;transition:all 1s ease 0s}.App #quote-box .display_buttons a .fab:hover{transition:all 1s ease 0s;color:#7F8688}.App #quote-box .display_buttons button{width:6em;background-color:#F3E367;border:1px solid #7F8688;font-family:"Roboto", Tahoma, sans-serif;font-weight:bold;color:#7F8688;cursor:pointer;transition:all 1s ease 0s;outline:none}.App #quote-box .display_buttons button:hover{border-radius:50px;transition:all 1s ease 0s;background-color:#7F8688;color:#F3E367}.App #quote-box .code{width:100%;text-align:center}.App #quote-box .code a{text-decoration:none;font-family:"Roboto", Tahoma, sans-serif;font-size:.75em;color:#F3E367}@media (min-width: 720px){.App #quote-box{width:600px}.App #quote-box .display_quote #text{font-size:2em}.App #quote-box .display_quote #author{font-size:1em}}

